.faq {
	@apply container--full grid grid-cols-1 gap-5 bg-siteGray lg:grid-cols-2 lg:gap-8;
	&--title {
		.title--wrap {
			@apply sticky top-20;
		}
	}
	&--list {
		@apply lg:mt-24;
	}
	&--ul {
		@apply border-t border-black;
		.item {
			@apply h-16 overflow-hidden border-b border-black lg:h-20;
			transition: height 0.4s ease;
			.question {
				@apply flex h-16 items-center justify-between lg:h-20;
				.title {
					@apply text-lg font-semibold text-black lg:text-xl;
				}
			}
			.answer {
				@apply pb-4 text-sm lg:pb-6 lg:text-base;
			}
			&.is-active {
				height: var(--h);
				.btn--more {
					@apply after:rotate-180;
				}
			}
		}
	}
}
